
























export default {
	data() {
		return {
			tasksDetails:{
				taskTitle:'',
				copyWriting:'',
				picId:'',
				picId2:'',
				taskIdentifier:"",//任务编号
				statusTask:''//任务状态
			},
			copyImages:[],
			orderInfo:{
				taskIdentifier:'',
				account:''
			}
		};
	},
	onLoad(){
		this.tasksDetails = uni.getStorageSync('tasksDetails');
		this.copyImages=[
			'https://www.sixkeep.com:8080/pics/'+this.tasksDetails.picId,
			'https://www.sixkeep.com:8080/pics/'+this.tasksDetails.picId2,
		];
		this.orderInfo.taskIdentifier = this.tasksDetails.taskIdentifier;
		this.orderInfo.account = uni.getStorageSync('userInfo').account;
	},
	methods:{
		getTask(){
			if(this.tasksDetails.statusTask !='01'){
				uni.showToast({
					icon:'none',
					title:'当前任务已领取，请勿重复领取'
				});
				return;
			}else{
			const jsonString = {
				orderInfo:this.orderInfo,
				requestType:"creatOrde",
			}
			const param ={
				controllerRequestType:"orderControllerService",
				jsonString:JSON.stringify(jsonString)
			}
			uni.request({
				url:this.websiteUrl,
				method:'POST',
				header: {
					"content-type":"application/x-www-form-urlencoded"
				},
				data:param,
				success(res) {
					console.log(res.data);
					if(res.data.errorCode =='0000'){
						uni.showToast({
							icon:'none',
							title:'领取任务成功！'
						})
						this.tasksDetails.statusTask = '08';
					}else{
						uni.showToast({
							icon:'none',
							title:res.data.errorMessage
						})
					}
				},
				fail(res) {
				uni.showToast({
					icon:'none',
					title:'网络异常！请稍后重试'
				})	
				}
			})	
			}
			
				
		},
		copywriting(){
			uni.setClipboardData({
				data: this.tasksDetails.copyWriting,
				success: function () {
					uni.showToast({
						title: '已复制到剪贴板',
						duration: 2000,
						icon:'none'
					});
				}
			});
		},
		
		saveImage(img){
			
			uni.showModal({
				title:'',
				content:"保存图片到相册吗？",
				success:function(result){
					if(result.confirm){		
						uni.downloadFile({
							url: img,
							success: (e) => {
								uni.saveImageToPhotosAlbum({
									filePath: e.tempFilePath,
									success: () => {
										uni.showToast({
											icon: "none",
											title: "已保存到手机相册"
										})
									},
									fail: () => {
										uni.showToast({
											icon: "none",
											title: "保存到手机相册失败"
										})
									}
								});
							},
							fail: (e) => {
								uni.showModal({
									content: "下载失败，" + e.errMsg,
									showCancel: false
								})
							}
						})
					}
					if(result.cancel){
						console.log("取消")
					}
				}
			});
		}
	}		
}



